
	/* eslint-disable */
	import ThemeButton from "@/components/ThemeButton.vue";
	import {
		ref
	} from 'vue';
	import {
		useStore
	} from 'vuex';

	const dark_mode = ref(false)

	export default {
		name: "NavBar",
		data() {
			return {
				dark_mode
			}
		},
		setup() {
			const store = useStore();
			const logout = () => {
				store.commit("logout");
			};

			return {
				logout,
			}
		},
	};
