
	/* eslint-disable */
	import ContentBase from '../components/ContentBase.vue'
	import {
		ref
	} from 'vue'
	import {
		ElMessageBox
	} from 'element-plus'

	const dialogVisible = ref(false)
	const handleClose = (done: () => void) => {
		ElMessageBox.confirm('Are you sure to close this dialog?')
			.then(() => {
				done()
			})
			.catch(() => {
				// catch error
			})
	}

	export default {
		name: "AboutView",
		components: {
			ContentBase,
		},
		data() {
			return {
				dialogVisible,
				carouseImgs: [
					{ url: require("../assets/photo1.png")},
					{ url: require("../assets/photo1.png")},
					{ url: require("../assets/photo1.png")},
					{ url: require("../assets/photo1.png")},
				]
			}
		}
	};
